<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <el-input placeholder="" v-model="filterInput"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="showAddForm"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="filterBy(communitys,filterInput)" style="width: 100%;">
            <el-table-column prop="name" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="decoration" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="style" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="num" label="" width="180" sortable>
            </el-table-column>
            <el-table-column prop="time" label="" width="120" sortable>
            </el-table-column>
            <el-table-column label="" min-width="240">
                <template slot-scope="props">
                    <el-button size="small"  icon="el-icon-edit" @click="showEditForm(props.row.id)"></el-button>
                    <el-button type="danger" size="small" @click="deleteCommunity(props.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
            <el-form v-on:submit="addCommunity">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="addcommunity.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="addcommunity.decoration" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="addcommunity.style" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="addcommunity.num" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="addcommunity.time" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="addCommunity"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
            <el-form v-on:submit="updateCommunity">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="community.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="community.decoration" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="community.style" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="addcommunity.num" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="community.time" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="updateCommunity"> </el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    export default {
        data(){
            return{
                filterInput:"",
                //
                addcommunity:{
                    addname: '',
                    adddecoration: '',
                    addstyle: '',
                    addnum:'',
                    addtime: ''
                },
                //
                community:{
                    name: '',
                    decoration: '',
                    style: '',
                    num:'',
                    time: ''
                },
                communitys:[],
                addFormVisible: false,//
                editFormVisible: false,//
                formLabelWidth: '120px'
            }
        },
        methods: {
            filterBy(communitys,value){
                return communitys.filter(function (community) {
                    return community.name.match(value);
                })
            },
            fetchCommunitys() {
                this.$http.get("http://localhost:3000/communitys").then(function (response) {
                    console.log(response);
                    this.communitys = response.body;
                })
            },
            //
            showAddForm() {
                this.addFormVisible = true
            },
            addCommunity(e){
                if(!this.addcommunity.name ||!this.addcommunity.decoration ||!this.addcommunity.style ||!this.addcommunity.num  ||!this.addcommunity.time){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                }else{
                    let newCommunity = {
                        name: this.addcommunity.name,
                        decoration: this.addcommunity.decoration,
                        style: this.addcommunity.style,
                        num: this.addcommunity.num,
                        time: this.addcommunity.time
                    }
                    this.$http.post("http://localhost:3000/communitys",newCommunity).then(function (response) {
                    })
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    e.preventDefault();
                }
                e.preventDefault();
                this.addFormVisible = false
                this.fetchCommunitys();
            },
            //
            showEditForm(id) {
                this.id = id;
                this.editFormVisible = true;
                this.$http.get("http://localhost:3000/communitys/"+id).then(function (response) {
                    this.community = response.body;
                });
            },
            updateCommunity(id){
                if(!this.community.name ||!this.community.decoration ||!this.community.style ||!this.community.num ||!this.community.time){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                }else{
                    let updateCommunity = {
                        name: this.community.name,
                        decoration: this.community.decoration,
                        style: this.community.style,
                        num: this.community.num,
                        time: this.community.time
                    }
                    this.$http.patch("http://localhost:3000/communitys/"+this.id,updateCommunity).then(function () {
                        this.fetchCommunitys();
                    });
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    this.editFormVisible = false;
                }
            },
            //
            deleteCommunity(id){
                this.$http.delete("http://localhost:3000/communitys/"+id).then(function () {
                    this.fetchCommunitys();
                });
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                });
            }
        },
        created(){
            this.fetchCommunitys();
        }
    }
</script>



