<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <el-input placeholder="" v-model="filterInput"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="showAddForm"></el-button>
                </el-form-item>
                <el-form-item>
                    :{{userNum}}
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="filterBy(users,filterInput)" style="width: 100%;">
            <el-table-column prop="name" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="age" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="address" label="" width="180" sortable>
            </el-table-column>
            <el-table-column prop="time" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="phone" label="" min-width="130" sortable>
            </el-table-column>
            <el-table-column prop="idCard" label="" min-width="180" sortable>
            </el-table-column>
            <el-table-column label="" width="240">
                <template slot-scope="props">
                    <el-button size="small"  icon="el-icon-edit" @click="showEditForm(props.row.id)"></el-button>
                    <el-button type="danger" size="small" @click="deleteUser(props.row.id)"></el-button>
                    <el-button type="primary" size="small" @click="addUserManagement(props.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
            <el-form v-on:submit="addUser">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.age" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.address" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.time" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.phone" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="adduser.idCard" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="addUser"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
            <el-form v-on:submit="updateUser">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.age" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.address" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.time" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.phone" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="user.idCard" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="updateUser"> </el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    export default {
        data(){
            return{
                userNum:'',
                filterInput:"",
                //
                adduser:{
                    addname: '',
                    addage: '',
                    addaddress: '',
                    addtime: '',
                    addphone: '',
                    addidCard: ''
                },
                //
                user:{
                    name: '',
                    age: '',
                    address: '',
                    time: '',
                    phone: '',
                    idCard: '',
                },
                users:[],
                addFormVisible: false,//
                editFormVisible: false,//
                formLabelWidth: '120px'
            }
        },
        methods: {
            filterBy(users,value){
                return users.filter(function (user) {
                    return user.name.match(value);
                })
            },
            fetchUsers() {
                this.$http.get("http://localhost:3000/users").then(function (response) {
                    console.log(response);
                    this.users = response.body;
                })
            },
            //
            showAddForm() {
                this.addFormVisible = true
            },
            addUser(e){
                if(!this.adduser.name ||!this.adduser.age ||!this.adduser.address  ||!this.adduser.time  ||!this.adduser.phone ||!this.adduser.idCard ){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                }else{
                    let newUser = {
                        name: this.adduser.name,
                        age: this.adduser.age,
                        address: this.adduser.address,
                        time: this.adduser.time,
                        phone: this.adduser.phone,
                        idCard: this.adduser.idCard,
                    }
                    this.$http.post("http://localhost:3000/users",newUser).then(function (response) {
                    })
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    e.preventDefault();
                }
                e.preventDefault();
                this.addFormVisible = false
                this.fetchUsers();
                this.getUserNum();
            },
            //
            showEditForm(id) {
                this.id = id;
                this.editFormVisible = true;
                this.$http.get("http://localhost:3000/users/"+id).then(function (response) {
                    this.user = response.body;
                });
            },
            addUserManagement(id){
                let updateUser = {
                    managementer: 1,
                }
                this.$http.patch("http://localhost:3000/users/"+id,updateUser).then(function () {
                });
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                });
            },
            updateUser(id){
                if(!this.user.name ||!this.user.age ||!this.user.address  ||!this.user.time  ||!this.user.phone ||!this.user.idCard ){
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'error'
                    });
                }else{
                    let updateUser = {
                        name: this.user.name,
                        age: this.user.age,
                        address: this.user.address,
                        time: this.user.time,
                        phone: this.user.phone,
                        idCard: this.user.idCard
                    }
                    this.$http.patch("http://localhost:3000/users/"+this.id,updateUser).then(function () {
                        this.fetchUsers();
                        // this.getUserNum();
                    });
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    this.editFormVisible = false;
                }
            },
            //
            deleteUser(id){
                this.$http.delete("http://localhost:3000/users/"+id).then(function () {
                    this.fetchUsers();
                    this.getUserNum();
                });
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                });
            },
            getUserNum(){
                this.$http.get("http://localhost:3000/users").then(function (response) {
                    this.userNum = response.body.length;
                })
            }
        },
        created(){
            this.fetchUsers();
            this.getUserNum();
        }
    }
</script>



