<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <el-input placeholder="" v-model="filterInput"></el-input>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="filterBy(users,filterInput)" style="width: 100%;">
            <el-table-column prop="name" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="age" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="address" label="" width="180" sortable>
            </el-table-column>
            <el-table-column prop="time" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="phone" label="" min-width="130" sortable>
            </el-table-column>
            <el-table-column prop="idCard" label="" min-width="180" sortable>
            </el-table-column>
            <el-table-column label="" width="240">
                <template slot-scope="props">
                    <el-button type="danger" size="small" @click="deleteUserManagement(props.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </section>
</template>

<script>
    export default {
        data(){
            return{
                filterInput:"",
                users:[],
                formLabelWidth: '120px'
            }
        },
        methods: {
            filterBy(users,value){
                return users.filter(function (user) {
                    return user.name.match(value);
                })
            },
            fetchUsers() {
                this.$http.get("http://localhost:3000/users/?managementer=1").then(function (response) {
                    console.log(response);
                    this.users = response.body;
                })
            },
            //
            deleteUserManagement(id){
                let updateUser = {
                    managementer: 0,
                }
                this.$http.patch("http://localhost:3000/users/"+id,updateUser).then(function () {
                    this.fetchUsers();
                });
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                });
            },
        },
        created(){
            this.fetchUsers();
        }
    }
</script>



