import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Community from "./views/nav1/Community";
import Building from "./views/nav1/Building";
import House from "./views/nav1/House";
import PropertyManagement from "./views/nav2/PropertyManagement";
import HouseMaintain from "./views/nav2/HouseMaintain";
import ApartmentDecor from "./views/nav2/ApartmentDecor";
import LeaseManagement from "./views/nav2/LeaseManagement";
import User from "./views/nav2/User";
import UserManagement from "./views/nav2/UserManagement";
import Complain from "./views/nav2/Complain";
import Utilities from "./views/nav3/Utilities";
import Deposit from "./views/nav3/Deposit";
import BillingRegistration from "./views/nav3/BillingRegistration";
import Billing from "./views/nav3/Billing";
import Arrearage from "./views/nav3/Arrearage";
import UserDeposit from "./views/nav4/UserDeposit";
import CostBreakdown from "./views/nav4/CostBreakdown";
import Collect from "./views/nav4/Collect";
import Greening from "./views/nav5/Greening";
import Clean from "./views/nav5/Clean";
import Facilities from "./views/nav5/Facilities";
import InternalSystems from "./views/nav6/InternalSystems";
import Personnel from "./views/nav6/Personnel";
import Corporate from "./views/nav6/Corporate";
import Office from "./views/nav6/Office";
import WorkSchedule from "./views/nav6/WorkSchedule";
import CommunityService from "./views/nav7/CommunityService";
import Security from "./views/nav7/Security";
import SecurityRecords from "./views/nav7/SecurityRecords";
import Pass from "./views/nav7/Pass";
import Fire from "./views/nav7/Fire";

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/main',
        component: Main,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-building',//class
        children: [
            { path: '/community', component: Community, name: '' },
            { path: '/building', component: Building, name: '' },
            { path: '/house', component: House, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/propertymanagement', component: PropertyManagement, name: '' },
            { path: '/housemaintain', component: HouseMaintain, name: '' },
            { path: '/housedecor', component: ApartmentDecor, name: '' },
            { path: '/leasemanagement', component: LeaseManagement, name: '' },
            { path: '/user', component: User, name: '' },
            { path: '/usermanagement', component: UserManagement, name: '' },
            { path: '/complain', component: Complain, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-calculator',
        children: [
            { path: '/utilities', component: Utilities, name: '' },
            { path: '/deposit', component: Deposit, name: '' },
            { path: '/billingregistration', component: BillingRegistration, name: '' },
            { path: '/billing', component: Billing, name: '' },
            { path: '/arrearage', component: Arrearage, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-search',
        children: [
            { path: '/userdeposit', component: UserDeposit, name: '' },
            { path: '/costbreakdown', component: CostBreakdown, name: '' },
            { path: '/collect', component: Collect, name: '' }

        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-camera-retro',
        children: [
            { path: '/greening', component: Greening, name: '' },
            { path: '/clean', component: Clean, name: '' },
            { path: '/facilities', component: Facilities, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-envelope-open',
        children: [
            { path: '/internalsys', component: InternalSystems, name: '' },
            { path: '/personnel', component: Personnel, name: '' },
            { path: '/corporate', component: Corporate, name: '' },
            { path: '/office', component: Office, name: '' },
            { path: '/workschedule', component: WorkSchedule, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-envelope-open-o',
        children: [
            { path: '/communityservice', component: CommunityService, name: '' },
            { path: '/security', component: Security, name: '' },
            { path: '/securityrecords', component: SecurityRecords, name: '' },
            { path: '/pass', component: Pass, name: '' },
            { path: '/fire', component: Fire, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



